.navbar {
    background: rgb(30, 30, 30);
    height: 5rem;
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto; 
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
}

.logo{
    object-fit: cover;
    height: 3.5rem;
    width: 3.5rem;
    border: 3px solid white;
    border-radius: 40px;
}

.desktopMenuListItem {
    margin: 1rem; 
    cursor: pointer;
}

/*For each item when the mouse hovers over it they would display:
A yellow solid 8px line which shows at the bottom of it like a highlight
*/
.desktopMenuListItem:hover{
    color: #03A062;
    padding-bottom: 0.5rem;
    border-bottom: 5px solid;
}

.active{
    color: green;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}

.desktopMenuBtn {
    background: white;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem;
}

.desktopMenuImg{
    object-fit: cover;
    height: 1rem; 
    width: 1rem; 
    margin: 1rem; 
}

.active {
    color: green;
    padding-bottom: 0.5rem; 
    border-bottom: 3px solid green;
}

.mobMenu{
    display: none;
    object-fit: cover;
    height: 2.5rem;
}

.navMenu{
    position: absolute;
    top: 4rem; 
    right: 2rem; 
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40,40,40);
    border-radius: 1rem;
}

.listItem{
    color: white;
    padding: 0.5rem 3 rem;
    margin: 0.25rem;
    background: rgb(30, 30, 30);
}

@media screen and (max-width: 720px){
    .mobMenu{
        display: flex;
    }
    .desktopMenu{
        display: none;
    }
    .desktopMenuBtn{
        display: none; 
    }
}