#skills{
    overflow: hidden;
    width: 100%;
    max-width: 65rem;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto; 
}

.skillTitle{
    margin: 1rem 0;
    font-size: 3rem;
    font-weight: 600;
    padding-top: 1rem;
    
}
.skillDesc{
    font-weight: 400;
    font-size: 1rem;
    max-width: 51rem; 
}

.skillBars{
    margin: 1.5rem; 
    max-width: auto;
    text-align: left; 
}

.skillBar{
    display: flex; 
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background: rgb(50, 50, 50); 
    align-items: center;
    border-radius: 10px;
}

.skillBarImg{
    object-fit: cover; 
    height: 3rem;
    width: 3rem;
    margin-right: 2rem;
    border-radius: 10px;
}

.skillBarText>p {
    font-size: 0.91rem;
    font-weight: 200;
}

@media screen and (max-width: 1280px){
    .skillTitle{
        padding-top: 10px;
        font-size: 2rem;
    }
    .skillDesc{
        margin-left: 5px;
        font-size: 1.3rem;
    }
    .skillBarText>h2{
        font-size: 2.5vw
    }

    .skillBarText>p{
        font-size: 1.5vw;
    }
}


