#contactPage{
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem; 
    margin: 0 auto;
    text-align: center;
    display: flex; 
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contactPageTitle{
    margin-top: 2.8rem;
    font-size: 3rem;
    margin-bottom: 1rem;
}

