#works {
    margin: 0 auto;
    min-height: calc(100vh - 4rem);
    width: 100%;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    overflow: hidden;
}

.worksTitle {
    margin: 1rem 0;
    font-size: 2rem;
    font-weight: 600;
    padding-top: 1.5rem;
    margin-bottom: 0.5rem;
}

.worksDesc {
    font-weight: 300;
    font-size: 1.3rem;
    max-width: 45rem;
}

.workImgs {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    max-width: 65rem;
    gap: 2rem;
    margin-top: 2rem;
}

.workContainer {
    flex: 0 0 calc(50% - 2rem);
    flex-direction: column;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background: rgb(50, 50, 50);
    align-items: center;
}

.workHeader {
    margin-bottom: 1rem;
}

.workHeader h2 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
}

.workContent {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.workContent p {
    font-size: 1rem;
    line-height: 1.6;
}

.worksImg {
    width: auto;
    height: 20rem;
    object-fit: cover;
    margin-right: 1.5rem;
}

.workBtn {
    margin: 3rem 0;
    padding: 0.5rem 2.5rem;
    border: none;
    border-radius: 2rem;
    background: white;
    font-size: 1rem;
}

.dropdownContent {
    margin-top: 1rem;
    padding: 1rem;
    background: rgb(50, 50, 50);
    border-radius: 0.5rem;
    text-align: center;
}

.iconContainer {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 1rem;
    margin-top: 1rem;
}

.link {
    width: 2rem;
    height: 2rem;
}

.seeLess {
    padding: 0.5rem 2.5rem;
    border: none;
    border-radius: 2rem;
    background: white;
    font-size: 1rem;
}

/* iPhone SE - Portrait */
@media screen and (max-width: 1280px){
    .workContent p {
        font-size: 1.25rem;
    }
}


/*IPad Air - Landscape*/
@media screen and (max-width: 1180px){
    .workContent p{
        font-size: 1.5rem;
    }
}

/* iPad Pro, Galaxy Tab S4 - Landscape */
@media screen and (max-width: 1025px) {
    .workContent p {
        font-size: 1rem; /* Adjust paragraph font size */
    }
    .worksImg{
        height: 20vw;
    }
}

/*IPad Air - Potrait*/
@media screen and (max-width: 821px){
    .workContent p {
        font-size: 0.8rem;
    }
    .worksImg{
        height: 20vw;
    }
}


@media screen and (max-width: 769px){
    .worksImg{
        height: 20vw
    }
    .workContent p {
        font-size: 0.8rem;
    }
}

/* iPhone SE - Landscape */
@media screen and (max-width: 668px) {
    .worksImg {
        height: 30vw;
    }
}

/*Iphone SE - Potrait */
@media screen and (max-width: 667px) {
    .worksImg {
        height: 20vw;
    }
    .workContent p {
        font-size: 0.8rem; 
    }
    .workHeader h2 {
        font-size: 1rem; 
    }
    .worksDesc, .workBtn, .dropdownContent, .iconContainer, .link, .seeLess {
        font-size: 0.8rem; 
    }
}

@media screen and (max-width: 540px){
    .workContent p {
       font-size: 0.8rem; 
    }
}

/*IPhone 14 Pro Max - Potrait*/
@media screen and (max-width: 431px){
    .workContent p {
        font-size: 0.65rem;
    }
}


/* iPhone 6/7/8 Plus, Galaxy S9/Note 9 - Portrait */
@media screen and (max-width: 414px) {
    .worksImg {
        height: 20vw;
    }
    .workContent p {
        font-size: 0.65rem;
    }
}

@media screen and (max-width: 412px){
    .workContent p {
        font-size: 0.70rem;
    }
}

/*Iphone SE - Potrait */
@media screen and (max-width: 376px) {
    .worksImg {
        height: 20vw;
    }
    .workContent p {
        font-size: 0.8rem; 
    }
    .workHeader h2 {
        font-size: 1rem; 
    }
    .worksDesc, .workBtn, .dropdownContent, .iconContainer, .link, .seeLess {
        font-size: 0.8rem; 
    }
}