@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100..900&display=swap");

#intro{
    height: calc(100vh - 4rem);
    width: 100vw; 
    max-width: 75rem; 
    margin: 0 auto;
    overflow: hidden; 
    position: relative; 
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 1rem;
    box-sizing: border-box;
}

.bg{
    position: absolute; 
    top: -4rem;
    right: 0; 
    z-index: -1;
    object-fit: cover; 
    height: 100vh; 
    width: auto;
}

.introContent{
    height: 100vh; 
    width: 100vw; 
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hello{
    font-family: Robota-Slab;
    font-size: 1.75rem;
    font-weight: 100;
}

.introName{
    color: #03A062;
    font-family: Robota-Slab;
}

.introPara{
    font-family: Robota-Slab;
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
}

.btn{
    background: white;
    margin: 1rem 0; 
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
}

.btnImg{
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
}

@media screen and (max-width: 1280px){

    .introContent {
        padding: 1rem; 
        font-size: 2.8rem; 
    }
    .hello {
        font-size: 2.5rem; 
    }
    .introName{
        font-size: 3.5rem
    }
    .introPara {
        font-size: medium; 
    }
    .btn {
        padding: 0.5rem 1rem; 
        font-size: 0.875rem; 
    }
}

